@import '../../Common/styles/scss/mixin.scss';
@import '../../Common/styles/scss/variables.scss';

.skill {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 20px;
  box-sizing: border-box;
  margin-bottom: 20px;
  margin: 0 auto;
  border: 1px solid $bgSecondaryColor;
  &:hover {
    background-color: $bgSecondaryColor;
  }
  .icon {
    height: 104px;
    width: 101px;
    border: 1px solid #61dafb;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;

    .iconItself {
      color: $primaryColor;
    }
  }
  .title {
    font-weight: 700;
    color: $primaryColor;
    font-size: 1.3rem;
  }
  .description {
    font-size: 1.2rem;
    line-height: 1.75em;
    font-weight: 400;
    color: $textColor;
    margin: 0 0 20px;
  }
}
